  --lirear: rgb(99, 158, 117);
  --large-screen: 1472px;
  --tablet-screen: 740px;
  --mobile-screen: 320px;
  --padding: 16px;
}

@font-face {
  font-family: "Elisabethische";
  src: local("Elisabethische"), url("../fonts/Elisabethische.woff") format("woff");
