  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: local("RubikMedium"), url("../fonts/RubikMedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.container {
  width: 100%;
