}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input {
  margin: 0;
  padding: 0;
  background-color: transparent;
  font-size: inherit;
  font-family: inherit;
  color: var(--white);
  border: 0;
  cursor: pointer;
}
input:hover, input:focus {
  outline: none;
}

.visually-hidden:not(:focus) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.h1 {
  font-family: "EIisabethische";
  font-style: normal;
