  background-color: transparent;
  line-height: 1;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
}

.btn--primary,
.btn--secondary {
  height: 50px;
  line-height: 50px;
  font-size: 12px;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
